.container {
	display: flex;
	flex-direction: row;
}

.menu {
	display: flex;
	flex-direction: column;
	background-color: #C4C4C4;
	height: 96vh;
	width: 13%;
	margin-top: 4vh;
}

.menu-button {
	display: flex;
	height: 50px;
	width: 100%;
	text-align: left;
	background-color: #C4C4C4;
	text-indent: 30px;
	line-height: 50px;
}

.menu-button-active {
	display: flex;
	height: 50px;
	width: 100%;
	text-align: left;
	background-color: #DADADA;
	text-indent: 30px;
	line-height: 50px;
}

.menu-button-active:hover {
	cursor:pointer;
}

.menu-button:hover {
	cursor:pointer;
	background-color: #D0D0D0;
}

.content {
	display: flex;
	flex-direction: column;
	flex: 1 1 auto;
	margin-top: 4vh;
	background-color: #FFF;
	font-weight: bold;
	text-align: left;

}

.main{
    width: 100%;
}

.category-header {
	font-weight: bold;
	text-align: left;
	text-indent: 130px;
	line-height: 80px;
	height: 80px;
	font-size: 30px;
}

.field-text {
	display: flex;
	flex-direction: row;
	justify-content: space-around;
	height: 55px;
}

.setting-name {
	font-weight: bold;
	text-align: left;
	text-indent: 30px;
	line-height: 55px;
	height: 55px;
	width: 200px;
}

.setting-padding {
	font-weight: normal;
	text-align: left;
	line-height: 55px;
	height: 55px;
	width: 200px;
}

.settings-input {
   	width: 400px;
  	padding: 12px 20px;
  	border: solid lightgrey 1px;
  	background-image: url('../../images/icon_edit.svg');
  	background-size: 20px 20px;
  	background-position: 98% 50%;
  	background-repeat: no-repeat;
}

.settings-input:hover {
	cursor: pointer;
}
